import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Logo from '/src/components/Logo/Logo.js'
import MenuList from './menu/menu-list.js'
import MenuSocial from './social/social-list'
import ThemeToggle from './theme-toggle/theme-toggle'
import './header.scss'

const Header = ({ siteTitle }) => {
  const menuItems = [
    {name:'Inicio',link:'/', partially: false},
    {name:'Articulos',link:'/articles', partially: true}
  ],
  socialItems = [
    {name: 'Github', link:'https://github.com/jlpa-dev', icon:'github'},
    {name: 'Facebook', link:'https://www.facebook.com/not.me.mby/', icon:'facebook'},
    {name: 'Instagram', link:'https://www.instagram.com/jorge.jsx/', icon:'instagram'},
    {name: 'LinkedIn', link:'https://www.linkedin.com/in/jorge-luis-parihuam%C3%A1n-arivilca-05687b121/', icon:'linkedin'},
  ];
  return (
    <header className="header">
      <div className="header__content">
        <Link to="/" className="logo-wrapper">
          <Logo />
        </Link>

        <input className="menu-btn" type="checkbox" id="menu-btn" />
        <label className="menu-icon" htmlFor="menu-btn"><span className="navicon"></span></label>
        
        <ThemeToggle items={socialItems} />
        <MenuSocial items={socialItems} />
        
        <MenuList items={menuItems} />
      </div>
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
